import Image from "next/image";
import Link from "next/link";

export default function Hero() {
    return (
        <section className="bg-gray-50">
            <div className="max-w-7xl mx-auto px-6 lg:px-8 py-16 flex flex-col-reverse md:flex-row items-center gap-10">

                {/* Left Content */}
                <div className="flex-1 text-center md:text-left">
                    <h1 className="text-4xl md:text-6xl font-bold text-gray-900 leading-tight">
                        Welcome to <span className="text-blue-600">AmronShop</span>
                    </h1>
                    <p className="mt-4 text-lg text-gray-600">
                        Discover the best fashion trends and lifestyle products at affordable prices.
                    </p>
                    <div className="mt-6 flex justify-center md:justify-start gap-4">
                        <Link
                            href="/"
                            className="px-6 py-3 bg-blue-600 text-white rounded-lg shadow hover:bg-blue-700 transition"
                        >
                            Shop Now
                        </Link>
                    </div>
                </div>

                {/* Right Image */}
                <div className="flex-1">
                    <Image
                        src="data:image/jpeg;base64," // apna image yaha daalna (public/hero.png)
                        alt="Hero Image"
                        width={600}
                        height={400}
                        className="rounded-2xl shadow-lg"
                    />
                </div>
            </div>
        </section>
    );
}
